  openActionsheet(item) {
    let buttons = [];
    for (let i = 0; i < this.itemType.length; i++) {
      buttons.push({
        text: this.itemType[i]['name'],
        role: 'destructive',
        handler: () => {
          item['type']=this.itemType[i]['value'];
          item['typename']= this.itemType[i]['name'];
        }
      })
    }
    this.actionsheet.create({
      buttons:buttons,
    }).present();
  }
