  saveData(){
    let month=this.month+1;
    let workdate='';
    let day='';
    if(this.day<10){
      day='0'+this.day;
    }else{
      day=this.day;
    }
    if(this.month<9){
      workdate=this.year+'-'+0+month+'-'+day;
      
    }else{
      workdate=this.year+'-'+month+'-'+day
    }
    let flag=false;
    let message=''
    for(let i=0;i<this.dataList.length;i++){
      this.dataList[i]['workdate']=workdate;
      this.dataList[i]['weekday']=this.weekDay;
      this.dataList[i]['userid']=this.uuid;
      
      if(this.dataList[i]['taskname']==''){
        flag=true;
        message=message+'任务名称不能为空,';
      }else if (  this.dataList[i]['hours']=='' ){
        flag=true;
        message=message+' '+'工作时间不能为空,';
      }
      // else if(this.dataList[i]['rate']=='' ){
      //   flag=true;
      //   message=message+' ' +'进度填写不能为空,'
      // }
      else if(this.dataList[i]['rate']>'100.00'){
        flag=true;  
        message=message+' ' +'任务进度不能大于100%';
      }

    }
    if(flag==false){
      let url ='prjRecord/prjRecordService/saveList';
      this.httpService.post(url,this.dataList,res=>{
        if(res.code==0){
          this.toastCtrl.create({
            message:'保存成功',
            position:'bottom',
            duration:1000,
          }).present();
          this.getMonthRecord();
          this.getOnedayData();

        }
      });
    }else{
      
      this.toastCtrl.create({
        message:message,
        position:'top',
        duration:1500
      }).present();
    }
  }
